.is-hidden-success {
  display: none;
}

.backdrop-success {
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal-success {
  width: 335px;
  height: 400px;
  background-color: #e8e8e2;
  border-radius: 19px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%)scale(1);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

@media only screen and (min-width: 768px) {
  .modal-success {
    width: 490px;
  }
}

.modal-btn-success-close {
  width: 22px;
  height: 22px;
  cursor: pointer;
  background-color: #e8e8e2;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media only screen and (min-width: 768px) {
  .modal-btn-success-close {
    width: 28px;
    height: 28px;
    top: 24px;
    right: 24px;
  }
}

.modal-btn-icon-success {
  width: 22px;
  height: 22px;
}

@media only screen and (min-width: 768px) {
